import { navigate } from "@reach/router";
import React, { useState } from "react";

export default function Project({bgColor, image, path, textBgColor, textBgColorDark, title }) {
  const [hovering, setHovering] = useState(false);

	const theme = document.documentElement.getAttribute("data-theme");

  const cursorStyle = {
    cursor: `pointer`,
  };

	const bgImageStyle = {
		backgroundImage: `url(${image})`,
		backgroundRepeat: "no-repeat",
		backgroundPosition: "center",
	}

  return (
		<div style={{...cursorStyle, ...bgImageStyle}} className={`${bgColor ?? "bg-violet-700"} flex justify-center items-center bg-cover h-64 w-96 rounded-xl`} onMouseEnter={() => setHovering(true)}  onMouseLeave={() => setHovering(false)} onClick={() => navigate("project/" + (path ?? ""))}>
			{ hovering ? (
				<p className={`${theme === "dark" ? textBgColorDark : textBgColor} bg-opacity-60 font-bold text-4xl p-1 text-center w-full pb-3`}>{title}</p>
			) : <></>
			}
		</div>
  );
}
