import React from "react";
import Project from "../components/Project.js";

import booomtag from "../images/booomtaglogo.png";
import robot from "../images/robot.png";
import youthenergy from "../images/verbruiksmeter.png";

export default function ProjectScreen() {
  return (
    <main>
      <div className="flex justify-center">
        <div className="flex flex-col items-center h-full">
          <div className="grid place-items-center grid-cols-1 md:grid-cols-2 lg:grid-cols-2 xl:grid-cols-3 gap-6 sm:max-w-sm md:max-w-4xl lg:max-w-7xl">
            <Project
              image={booomtag}
							path={"booomtag"}
              textBgColor={"bg-white"}
              textBgColorDark={"bg-violet-600"}
              title={"Booomtag"}
            />
            <Project
              bgColor={"bg-violet-700"}
              image={robot}
							path={"robotica"}
              textBgColor={"bg-white"}
              textBgColorDark={"bg-violet-600"}
              title={"Robotica"}
            />
            <Project
              image={youthenergy}
							path={"verbruiksmeter"}
              textBgColor={"bg-white"}
              textBgColorDark={"bg-violet-600"}
              title={"Energie verbruiksmeter"}
            />
          </div>
        </div>
      </div>
    </main>
  );
}
